import ToastComponent from './tips.vue'

const Tips = {};

// Toast
Tips.install = function (Vue) {
    // Vue
    // 
    const ToastConstructor = Vue.extend(ToastComponent)
    // 
    const instance = new ToastConstructor();

    // div
    // div
    instance.$mount(document.createElement('div'))
    document.body.appendChild(instance.$el)

    // Vue
    //  
    Vue.prototype.$tips = (msg) => {
        instance.message = msg;

        // setTimeout(() => {
        //     instance.show = false;
        // }, duration);
        console.log(msg)
        console.log("11111111111111111111111111111111111")
        instance.show = true;
        // if (msg == 'register') {
        //     instance.showType = 1
        // } else if (msg == ) {
        //     instance.showType = 2
        // }


        switch (msg) {
            case '':
                instance.showType = 1;
                break;
            case '':
                instance.showType = 2;
                break;
            case '':
                instance.showType = 3;
                break;
            case '':
                instance.showType = 4;
                break;
            case '':
                instance.showType = 5;
                break;
            case '':
                instance.showType = 6;
                break;
            case '':
                instance.showType = 7;
                break;
            default:
                instance.showType = 10;
                instance.showMsg = msg;
        }



    }
}

// console.log(Toast)
export default Tips


