import Vue from 'vue';
import Router from 'vue-router';

import home from '@/pages/home/index';
import goodsDetail from '@/pages/home/goodsDetail'; //
import goodsList from '@/pages/home/goodsList'; //
import searchPage from '@/pages/home/searchPage'; //



import classify from '@/pages/classify/classify';
import shopMall from '@/pages/shopMall/shopMall';

import account from '@/pages/account/account';
import setUp from '@/pages/account/setUp';
import addressMag from '@/pages/shopCart/addressMag';
import addressEdit from '@/pages/shopCart/addressEdit';



import shopCart from '@/pages/shopCart/shopCart';
import order from '@/pages/shopCart/order';
import orderManage from '@/pages/shopCart/orderManage';
import orderDetails from '@/pages/shopCart/orderDetails';
import logisticsDetail from '@/pages/shopCart/logisticsDetail';



import login from '@/pages/loginRegister/login';
import passSetUp from '@/pages/loginRegister/passSetUp';
import passSetSucess from '@/pages/loginRegister/passSetSucess';
import register from '@/pages/loginRegister/register';
import registerSucess from '@/pages/loginRegister/registerSucess';

import { homedir } from 'os';

import Layout from '@/pages/layout/Layout'
import getInfo from './hook/getInfo';
Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/layout/home',
    },
    {
      path: '/layout',
      name: 'layout',
      component: Layout,
      children: [
        {
          path: 'home',
          name: 'home',
          component: home,
          meta: { title: '' },
        },
        {
          path: 'shopMall',
          name: 'shopMall',
          component: shopMall,
          meta: { title: '' },
        },
        {
          path: 'shopCart',
          name: 'shopCart',
          component: shopCart,
          meta: { title: '', requireAuth: true },
        },
        {
          path: 'account',
          name: 'account',
          component: account,
          meta: {
            title: '',
            requireAuth: true
          },
        },
        {
          path: 'classify',
          name: 'classify',
          component: classify,
          meta: { title: '' },
        }
      ]
    },
    {
      path: '/searchPage',
      name: 'searchPage',
      component: searchPage,
      meta: { title: '' },
    },
    {
      path: '/goodsList',
      name: 'goodsList',
      component: goodsList,
      meta: { title: '', keepAlive: true },
    },
    {
      path: '/addressMag',
      name: 'addressMag',
      component: addressMag,
      meta: { title: '', requireAuth: true },

    },
    {
      path: '/addressEdit',
      component: addressEdit,
      meta: { title: '', requireAuth: true },
      name: 'addressEdit',

    },
    {
      path: '/setUp',
      name: 'setUp',
      component: setUp,
      meta: { title: '', requireAuth: true },
    },
    {
      path: '/goodsDetail/:goodId',
      name: 'goodsDetail',
      component: goodsDetail,
      meta: { title: '' },
    },
    {
      path: '/order',
      name: 'order',
      component: order,
      meta: { title: '', requireAuth: true },
    },
    {
      path: '/orderManage',
      name: 'orderManage',
      component: orderManage,
      meta: { title: '', requireAuth: true },
    },
    {
      path: '/logisticsDetail',
      name: 'logisticsDetail',
      component: logisticsDetail,
      meta: { title: '', requireAuth: true },
    },
    {
      path: '/orderDetails/:orderId',
      name: 'orderDetails',
      component: orderDetails,
      meta: { title: '', requireAuth: true },
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: { title: '' },
    },
    {
      path: '/passSetUp',
      name: 'passSetUp',
      component: passSetUp,
      meta: { title: '' },
    },
    {
      path: '/passSetSucess',
      name: 'passSetSucess',
      component: passSetSucess,
      meta: { title: '' },
    },
    {
      path: '/register',
      name: 'register',
      component: register,
      meta: { title: '' },
    },
    {
      path: '/registerSucess',
      name: 'registerSucess',
      component: registerSucess,
      meta: { title: '' },
    },

  ],
})

// router.beforeEach((to, from, next) => {
//   if (to.meta.requireAuth) {// 
//     console.log(to)
//     if (getToken()) {// 
//       next()
//     } else {// 
//       next({
//         redirect: '/login',
//         query: { redirect: to.fullPath }
//       })
//     }
//   } else {
//     next()
//   }
// })
getInfo(router)
export default router



